import popModal from '../../../components/popmodal.vue'
var _this;
export default{
	data(){
		return{
			designr:{
				name:'',
				mobile:'',
				token:'',
				vcode:''
			},
			isShow:false,
			modalName:'',
			checkTel:false,
			checkPwd:false
			
		}
	},
	components:{
		popModal	
	},
	onLoad(){

	},
	methods:{
		//登录
		toLogin(){
			uni.navigateTo({
				url:'../login/login'
			})
		},
		//注册
		design(){
			_this=this;
			if(this.checkPwd && this.checkTel){
				uni.request({
					url:this.$store.state.url,
					data:{
						name:this.designr.name,
						token:this.designr.token,
						mobile:this.designr.mobile,
						gender:1,
						type:4,
						vcode:this.designr.vcode,
						f:'Signup'
					},
					success:(res)=>{
						if(res.data.data==-2){
							_this.isShow=true;
							_this.modalName='exit'
							setTimeout(function(){
								_this.isShow=false;
								_this.modalName=''
							},2000)
						}else{
							_this.isShow=true;
							_this.modalName='success'
							setTimeout(function(){
								_this.isShow=false;
								_this.modalName='',
								console.log('1111')
								uni.navigateTo({
									url:'../login/login?account='+_this.designr.mobile+'&token='+_this.designr.token
								})
							},500)
						}
						
					}
				})
			}else{
				uni.showToast({
					title:'所填内容不符合规则',
					icon:'none'
				})
			}
			
		},
		hideModal(e) {
			this.modalName = null;
			this.isShow=false;
		},
		clearPwd(event){
			this.designr.token=''
		},
		checkTelEvent(event){
			console.log(event)
			if(event){
				if(!(/^1[34578]\d{9}$/.test(event))){ 
					
						uni.showToast({
							title:'电话号码不存在',
							icon:'none'
						})
					} else{
						this.checkTel=true;
					}
				}
			},
		//验证密码
		checkPwdEvent(event){
			if(event){
				var reg=/^[a-zA-Z0-9]{6,12}$/;   
				if(reg.test(event)==false){
					uni.showToast({
						title:'密码不能含有非法字符，长度在6-12之间',
						icon:'none'
					})
				}else{
					this.checkPwd=true;
				}
				
			}
		}
			
		
	}
}
